import React from 'react';
import {UnderlinedLink} from 'styles/styled-components';
import {Point, KeywordsTitle, Keywords} from 'components/Project/styles';
import Project from 'components/Project';

const SoloChat = () => (
  <Project href="https://github.com/yifei2016" title="Solo Chat(Hobby project)">
    <Point>
      En chat sidan som imiterar Slack program, man anvnder sig med denna fr
      att skicka meddelanden, efter man tryckt p skick ikonen, man kan se meddelandet p
      chatt omrdet,
      om man skrev olika kommando som brjar med slash, till exempel,
      om man skrev /time s nuvarande tiden skickas till chatt omrdet,
      om man skrev /goodbye s goodbye meddelandet skickas till chatt omrdet,
      om man skrev /starwars s kommer det frsta resultatet skickas till chatt
      omrdet av en API call. Det r lget till det hr projeket just nu.
    </Point>
    <Point>
      <KeywordsTitle>Nyckelord:</KeywordsTitle>&nbsp;&nbsp;
      <Keywords>
        HTML5, CSS3, JavaScript 6, Bootstrap 4, React.js, Redux, node.js, Axios,
        Ajax, REST, Styled component,Git, refactor metod.
      </Keywords>
    </Point>
  </Project>
);

export default SoloChat;


