import React, { Component } from 'react';
import 'css/main.css';
import YifeiInfo from 'components/YifeiInfo';
import RightPanel from 'components/RightPanel';
import MouseScroll from 'components/MouseScroll';
import ReactDOm from 'react-dom';
import {BrowserRouter, Route, Link} from 'react-router-dom';
import {Wrapper, Main, Sides, Left, Right} from './styles';

class PersonalLetter extends Component {
  render() {
      return (
      <Wrapper >
        <Main>
          <Sides>
            <Left><YifeiInfo iconColor='darkgrey'/></Left> 
            <Right>
              Yifei Wang heter jag som pluggar Front end utveckling pa EC- utbildning och tar praktiken hos Fully Studios, 
              Jag ar fortjust i mitt karriar omradet och har stor passion att utveckla mig.
              <br/><br/>
              Innan jag pluggar pa EC yrkeshogskolan hade jag erfarenheter med redovisning, nar jag jobbade med ekonomi, det mesta jobbet ar gjort av program och jag insag alla branscher gar till digital, web utveckling ger man visuell feedback, det ar haftigt. Sen borjade jag lara mig sjalv python programmering sprak, jag kom pa mig att vara intresserad av programmering, darmed borjade jag min utbildning som ar ganska bra och gav mig grundlaggande till avancerad kunskaperna av web design, front end och backend utveckling. Forutom skolan, har jag ocksa gjort nagra hobby projekt som publiceras pa min Github.
              <br/><br/>
              Jag ar flitig, prestigelos och har beharskat vassa tekniker for att fa jobbet, jag ar sugen pa att erovra mer pa webb utvecklingen.
              Jag tycker om att arbeta i grupp och jag anses vara en flexibel, initiativtagande, samarbetsvillig och malinriktad person. Mina styrkor ar att jag ar nyfiken pa nya saker och har snabb inlarningsformaga.
              <br/><br/>
              Pa fritiden forsoker jag vara ute och rora pa mig sa mycket som mojlighet. Jag tycker om att promenera i skogen och spela badminton.
              Med detta hoppas jag att jag har vackt din intressen, jag kommer garna pa besok och beratta mer om mig sjalv.
            </Right>
          </Sides>
        </Main>
      </Wrapper>
    );
  }
}

export default PersonalLetter;




