import React from 'react';
import {UnderlinedLink} from 'styles/styled-components';
import {Point, KeywordsTitle, Keywords} from 'components/Project/styles';
import Project from 'components/Project';

const MapGo = () => (
  <Project href="https://github.com/yifei2016/localStorage-firebase" title="MapGo">
    <Point>
    I den hr skolans grupp projekt, har jag gjort en frstudie med SCRUM-team om vad 
    vi ska gra med projekt, vi har anvnt kodgranskning och parprogramering fr delar 
    av projektet, vi hade SCRUM-mten tv gnger per vecka och en sprint varje vecka. 
    Vi anvnde GIt och GitHub fr versionshantering, vi har gjort mockup och wireframe 
    ocks innan vi brjade koda. Applikationen har en responsiv design med Flexbox och 
    Media queries, man kan hmta data dynamiskt med AJAX frn olika ppna API:er. 
    Vi har anvnt etablerade principer fr UX design och layout regler, applikationen har 
    en vl genomtnkt och uttryckt design.
    </Point>
    <Point>
      <KeywordsTitle>Nyckelord:</KeywordsTitle>&nbsp;&nbsp;
      <Keywords>HTML5, CSS3, JavaScript, Photoshop, flex, Media queries, AJAX, Git, SCRUM, Agile metoder.</Keywords>
    </Point>
  </Project>
);

export default MapGo;


