import React from 'react';
import {UnderlinedLink} from 'styles/styled-components';
import {Point, KeywordsTitle, Keywords} from 'components/Project/styles';
import Project from 'components/Project';

const ClientZone = () => (
  <Project href="https://github.com/yifei2016" title="Client Zone">
    <Point>
    Ett projekt som anvnder sig vue.js som front end ramverk och mysql databas, l
    aravel som backend ramverk till PHP. Kunder kan se olika projekt info p webbsidan 
    och admin kan CRUD projekt efter validering med projekts namn, status och link infomationer. 
    Som kund kan man ladda upp video och filen via webbsidan och mjukt, 
    hrd ta bort data som hmtas frn mysql databasen. 
    Alla anvndare kan se sina projekt med client attribute genom teknik av ORM Relational Model 
    i laravel, Min roll r att jobba som full stack utvecklare, frn design till backend och 
    ven deploy och QA.
    </Point>
    <Point>
    <KeywordsTitle>Nyckelord:</KeywordsTitle>&nbsp;&nbsp;
    <Keywords>
      HTML5, CSS3, JavaScript 6, Bootstrap 4, vue.js, node.js, mysql, php, laravel, ORM, 
      Axios, Ajax, REST, Sequel pro, Sass, BEM,Git, SCRUM, Agile metod, Jira, Browserstack, QA, 
      Bitbucket.
    </Keywords>
    </Point>
  </Project>
);

export default ClientZone;


