import React from 'react';
import {UnderlinedLink} from 'styles/styled-components';
import {Point, KeywordsTitle, Keywords} from 'components/Project/styles';
import Project from 'components/Project';

const AutentiseringSidan = () => (
  <Project title="AutentiseringSidan" href="https://github.com/yifei2016/firebase-authentication">
    <Point>
      En sidan anvnder Firebase och autentisera mot GitHub.
    I webbsidan finns en knapp som man kan klicka p fr att stta igng autentisering mot GitHub,
    om autentisering lyckas s man navigeras till nsta sidan som visar man r inloggad och
    anvndarens namn och profilbild, dessutom kan man logga ut genom att klicka p logga ut
    knappen p sidan, om autentisering misslyckas ska sidan visa ett felmeddelande.
    </Point>
    <Point>
      <KeywordsTitle>Nyckelord:</KeywordsTitle>&nbsp;&nbsp;
      <Keywords>HTML5, CSS3, JavaScript, Firebase, GitHub, AJAX, sessionStorage, JSON, DOM, Git. </Keywords>
    </Point>
  </Project>
);

export default AutentiseringSidan;


